@font-face {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local(Source Sans Pro), url("SourceSansPro-Black.9aaf0f28.otf") format("opentype");
}

@font-face {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(Source Sans Pro), url("SourceSansPro-Bold.7c692663.otf") format("opentype");
}

@font-face {
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(Source Sans Pro), url("SourceSansPro-Regular.0895a32c.otf") format("opentype");
}

:root {
  --theme-primary: #ffb30e;
  --theme-secondary: #2780ed;
  --theme-warning: #f17228;
  --theme-gray-50: #fcfcfc;
  --theme-gray-100: #f5f5f5;
  --theme-gray-200: #eee;
  --theme-gray-300: #e0e0e0;
  --theme-gray-400: #bdbdbd;
  --theme-gray-500: #9e9e9e;
  --theme-gray-600: #757575;
  --theme-gray-700: #616161;
  --theme-gray-800: #424242;
  --theme-gray-900: #212121;
  --theme-white: #fff;
  --theme-black: #000;
  --theme-success: #79b93c;
  --font-family: "Source Sans Pro";
  --font3: "Open Sans", sans-serif;
}

body {
  font-family: var(--font-family);
}

.container {
  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 220px;
  padding-right: 220px;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo-svg {
  margin-right: 4px;
}

.logo p {
  letter-spacing: -.03em;
  color: var(--theme-warning);
  font-size: 31px;
  font-weight: 700;
  line-height: 1.2;
}

.logo p:last-child {
  color: var(--theme-primary);
}

.logo span {
  font-size: 26px;
}

.block-location, .block-location a {
  align-items: center;
  display: flex;
}

.block-location p {
  color: var(--theme-gray-800);
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
}

.block-location a {
  text-align: center;
  color: var(--theme-gray-800);
  margin-left: 12px;
  margin-right: 4px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.4;
}

.block-location svg {
  fill: #ffb30e;
  margin-right: 9px;
}

.navigation {
  align-items: center;
  column-gap: 30px;
  display: flex;
}

.navigation a {
  text-align: center;
  color: var(--theme-gray-800);
  fill: #ffb30e;
  align-items: center;
  column-gap: 8px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  display: flex;
}

.navigation button {
  background: var(--theme-white);
  border-radius: 8px;
  column-gap: 10px;
  padding: 14px 24px;
  transition: all .2s cubic-bezier(.165, .84, .6, 1);
  display: flex;
}

.navigation button:hover {
  box-shadow: 0 20px 40px #ffae004a, 0 5px 10px #ffae0042;
}

.navigation p {
  text-align: center;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(150deg, #ffb800 0%, #ff8a00 100%) text;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.1;
}

.section-hero {
  background: image-set("baground-hero.8a31fa8d.png" 1x, "baground-hero.5bdc42ec.avif" 1x type("image/avif"), "baground-hero.a31bd998.webp" 1x type("image/webp")) center / cover;
  width: 100%;
  height: 100%;
}

@media (min-device-pixel-ratio: 2), (resolution >= 192dpi), (resolution >= 2x) {
  .hero-image {
    background: image-set("baground-hero.8a31fa8d.png" 1x, "baground-hero.ea9b4b77.avif" 1x type("image/avif"), "baground-hero.6072866c.webp" 1x type("image/webp")) center / cover;
  }
}

@media (min-device-pixel-ratio: 3), (resolution >= 288dpi), (resolution >= 3x) {
  .hero-image {
    background: image-set("baground-hero.8a31fa8d.png" 1x, "baground-hero.e94a496a.avif" 1x type("image/avif"), "baground-hero.08232cf7.webp" 1x type("image/webp")) center / cover;
  }
}

.hero {
  padding-top: 132px;
  padding-bottom: 132px;
  position: relative;
  overflow: hidden;
}

.hero-card {
  z-index: 1;
  position: relative;
}

.hero-card h1 {
  color: var(--theme-white);
  margin-bottom: 15px;
  font-size: 88px;
  font-weight: 700;
  line-height: 1;
}

.hero-card p {
  color: #504f4f;
  font-size: 22px;
  font-weight: 400;
  line-height: 1.2;
}

.hero-form {
  background-color: var(--theme-white);
  border-radius: 16px;
  width: 856px;
  margin-top: 32px;
}

.hero-form .wrapper-label {
  border-bottom: 1px inset var(--theme-gray-200);
  column-gap: 8px;
  padding: 24px;
  display: flex;
}

.label-delivery div {
  cursor: pointer;
  color: var(--theme-gray-600);
  fill: var(--theme-gray-600);
  column-gap: 10px;
  padding: 10px 24px;
  display: flex;
}

.label-delivery input[type="radio"] {
  display: none;
}

.label-delivery input:checked + div {
  color: var(--theme-warning);
  fill: var(--theme-warning);
  background-color: #f172281a;
  border-radius: 8px;
}

.hero-form p {
  font-family: var(--font-family);
  color: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
}

.icon-delivery {
  fill: inherit;
}

.label-adress {
  align-items: center;
  column-gap: 16px;
  padding: 24px;
  display: flex;
  position: relative;
}

.icon-location {
  fill: #ff7474;
  position: absolute;
  left: 40px;
}

.label-adress input {
  background-color: var(--theme-gray-100);
  border: none;
  border-radius: 8px;
  outline: none;
  flex-grow: 1;
  padding: 12px 4px 12px 46px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.4;
}

.label-adress input::placeholder {
  color: var(--theme-gray-500);
}

.label-adress button {
  fill: var(--theme-white);
  text-align: center;
  color: var(--theme-white);
  background: linear-gradient(318deg, #ff7a7a 0%, #f65900 100%);
  border-radius: 8px;
  align-items: center;
  column-gap: 10px;
  padding: 21px 48px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  display: flex;
}

.hero-image {
  filter: brightness(1.4);
  cursor: pointer;
  background: image-set("hero.c842b22d.png" 1x, "hero.09a54788.avif" 1x type("image/avif"), "hero.0922721d.webp" 1x type("image/webp")) center / contain no-repeat;
  border-radius: 50%;
  width: 497px;
  height: 497px;
  animation-name: rotate-hero-img;
  animation-duration: 4s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-delay: 0s;
  position: absolute;
  bottom: -65px;
  right: 220px;
}

.hero-image-boxshadow {
  border-radius: 50%;
  width: 497px;
  height: 497px;
  position: absolute;
  bottom: -65px;
  right: 220px;
  box-shadow: -60px 60px 300px #37373799, -150px 80px 250px #373737cc;
}

.hero-image:hover {
  animation-play-state: paused;
}

@keyframes rotate-hero-img {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (min-device-pixel-ratio: 2), (resolution >= 192dpi), (resolution >= 2x) {
  .hero-image {
    background: image-set("hero.c842b22d.png" 1x, "hero.2ee10768.avif" 1x type("image/avif"), "hero.2a99a915.webp" 1x type("image/webp")) center / contain no-repeat;
  }
}

@media (min-device-pixel-ratio: 3), (resolution >= 288dpi), (resolution >= 3x) {
  .hero-image {
    background: image-set("hero.c842b22d.png" 1x, "hero.cd3dd72b.avif" 1x type("image/avif"), "hero.1f302f32.webp" 1x type("image/webp")) center / contain no-repeat;
  }
}

.sale-menu {
  padding-top: 80px;
  padding-bottom: 80px;
}

.list-sale-menu a {
  width: 100%;
  display: inline-block;
}

.list-sale-menu a:hover img {
  box-shadow: 0 4px 4px #00000040;
}

.list-sale-menu p {
  color: var(--theme-gray-800);
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
}

.list-sale-menu .sale-menu-p-decor {
  color: var(--theme-warning);
  background-color: #f1722833;
  border-radius: 8px;
  margin-top: 14px;
  padding: 8px 16px;
  display: inline-block;
}

.list-sale-menu img {
  border-radius: 20px;
  margin-bottom: 32px;
  display: block;
}

.list-sale-menu {
  justify-content: space-between;
  display: flex;
}

.wrapper-picture {
  position: relative;
}

.dish-discount {
  background-color: var(--theme-primary);
  border-radius: 0 32px 0 20px;
  align-items: center;
  padding: 10px 18px 11px;
  display: inline-flex;
  position: absolute;
  bottom: 0;
}

.dish-discount p {
  color: var(--theme-white);
  font-size: 68px;
  font-weight: 700;
  line-height: 1.06;
}

.dish-discount div {
  flex-direction: column;
  display: flex;
}

.dish-discount div > p {
  color: var(--theme-white);
  font-size: 35px;
  line-height: .9;
}

.dish-discount div > p:last-child {
  color: var(--theme-white);
  font-size: 22px;
  font-weight: 400;
  line-height: .8;
}

.how-does-it-work {
  padding-top: 80px;
  padding-bottom: 80px;
  position: relative;
}

.section-how-does-it-work {
  position: relative;
}

.shadow-how-does-it-work:before {
  content: "";
  background: linear-gradient(#ffce6738 0%, #efe0bf38 50%, #ffffff38 100%);
  width: 100%;
  height: 150px;
  position: absolute;
  top: 0;
  left: 0;
}

.how-does-it-work h2 {
  text-align: center;
  color: var(--theme-warning);
  text-align: center;
  margin-bottom: 60px;
  font-size: 43px;
  font-weight: 700;
  line-height: 1.12;
}

.list-how-work {
  text-align: center;
  column-gap: 32px;
  display: flex;
}

.list-how-work h3 {
  text-align: center;
  color: #434343;
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
}

.list-how-work p {
  font-family: var(--font3);
  text-align: center;
  color: var(--theme-gray-500);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.4;
}

.popolar-items {
  padding-top: 80px;
  padding-bottom: 80px;
}

.popolar-items h2 {
  text-align: center;
  color: var(--theme-gray-900);
  margin-bottom: 88px;
  font-size: 43px;
  font-weight: 700;
  line-height: 1.12;
}

.list-popular-items {
  display: flex;
}

.list-popular-items img {
  border-radius: 20px;
}

.list-popular-items a {
  display: inline-block;
}

.list-popular-items picture {
  margin-bottom: 16px;
  display: inline-block;
}

.list-popular-items h3 {
  color: var(--theme-gray-800);
  margin-bottom: 8px;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
}

.list-popular-items p {
  color: var(--theme-gray-900);
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
}

.list-popular-items div {
  fill: var(--theme-primary);
  align-items: center;
  column-gap: 8px;
  margin-bottom: 8px;
  display: flex;
}

.list-popular-items div > p {
  color: var(--theme-primary);
  font-weight: 400;
}

.list-popular-items button {
  background: var(--theme-warning);
  color: var(--theme-white);
  text-align: center;
  border-radius: 8px;
  width: 100%;
  margin-top: 16px;
  padding-top: 21px;
  padding-bottom: 21px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  box-shadow: 0 20px 40px #fa63233d, 0 5px 10px #fd725c38;
}

.container-swiper {
  position: relative;
}

.swiper {
  margin-top: 200px;
  overflow: hidden;
}

.swiper-button-lock {
  display: block;
}

.swiper-button-prev, .swiper-button-next {
  cursor: pointer;
  background: #ffb20e;
  border: 1px solid #faaa01;
  border-radius: 92px;
  width: 72px;
  height: 72px;
  margin-top: 0;
  padding: 10px 9px 10px 11px;
  top: 50%;
  box-shadow: 0 14px 32px #ffb20e4a, 0 5px 8px #de97003d;
}

.swiper-button-prev {
  position: absolute;
  left: -110px;
}

.swiper-button-next {
  position: absolute;
  right: -110px;
}

.swiper-button-prev:after {
  content: url("icon-prev.5a06813a.svg");
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
}

.swiper-button-next:after {
  content: url("icon-next.583826ab.svg");
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
}

.section-featured-restaurants {
  padding-bottom: 80px;
}

.section-featured-restaurants h3 {
  text-align: center;
  color: var(--theme-gray-900);
  margin-bottom: 88px;
  font-size: 43px;
  font-weight: 700;
  line-height: 1.12;
}

.list-featured-restaurants {
