}

.block-featured-restaurants {
  align-items: center;
  column-gap: 24px;
  display: flex;
}

.sticker-block {
  z-index: 1;
  column-gap: 8px;
  display: flex;
  position: absolute;
  top: 24px;
  left: 24px;
}

.sticker-block div:first-child {
  background-color: var(--theme-warning);
  border-radius: 8px;
  align-items: center;
  column-gap: 2px;
  padding: 8px 16px;
  display: flex;
}

.sticker-block div:last-child {
  background-color: var(--theme-primary);
  border-radius: 8px;
  align-items: center;
  column-gap: 2px;
  padding: 8px 16px;
